import { useEffect, useState } from "react";
import "./App.css";
import externalData from "./data.json";
import {
  Box,
  Button,
  ButtonGroup,
  ChakraProvider,
  Drawer,
  DrawerBody,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Flex,
  Heading,
  Progress,
  useDisclosure,
} from "@chakra-ui/react";
import React from "react";

const App: React.FC = () => {
  return (
    <ChakraProvider>
      <NameComepetition />
    </ChakraProvider>
  );
};

const NameComepetition = () => {
  const [data] = useState<Array<string>>([...externalData.names]);
  const [matchUps, setMatchUps] = useState<Array<Array<string>>>([]);
  const [is_loading, set_is_loading] = useState<boolean>(true);
  const [current_index, set_current_index] = useState<number>(0);
  const [winners, set_winners] = useState<Array<string>>([]);
  const [losers, set_losers] = useState<Array<string>>([]);
  const btnRef = React.useRef<HTMLButtonElement>(null);
  const [round, setRound] = useState(1);
  const { isOpen, onOpen, onClose } = useDisclosure();

  const get_randomized_names = (
    list_of_names: Array<string>,
  ): Array<string> => {
    return list_of_names
      .map((d) => {
        return { data: d, number: Math.random() };
      })
      .sort((a, b) => a["number"] - b["number"])
      .map((d) => d["data"])
      .slice(0, 50);
  };

  const get_two_length_arrays = (list_of_names: Array<string>) => {
    let names_in_two = [];
    for (let index = 0; index < list_of_names.length - 1; index += 2) {
      names_in_two.push([list_of_names[index], list_of_names[index + 1]]);
    }
    return names_in_two;
  };

  const set_outcome = (winner_index: number) => {
    const [temp_winners, temp_losers] =
      winner_index === 0
        ? [
            [...winners, matchUps[current_index][0]],
            [...losers, matchUps[current_index][1]],
          ]
        : [
            [...winners, matchUps[current_index][1]],
            [...losers, matchUps[current_index][0]],
          ];
    set_winners(temp_winners);
    set_losers(temp_losers);

    if (current_index + 1 === matchUps.length) {
      set_current_index(0);
      setRound(round + 1);
      const randomized_winners = get_randomized_names(temp_winners);
      const temp_matchups = get_two_length_arrays(randomized_winners);
      setMatchUps(temp_matchups);
      if (temp_matchups.length > 0) {
        set_winners([]);
      }
    } else {
      set_current_index(current_index + 1);
    }
  };

  const init = () => {
    const temp_data = get_randomized_names(data);
    set_current_index(0);
    setRound(1);
    setMatchUps(get_two_length_arrays(temp_data));
    set_is_loading(false);
    set_winners([]);
    set_losers([]);
  };

  useEffect(() => {
    if (is_loading) {
      init();
    }
  });

  return (
    <>
      <Flex
        direction="column"
        alignItems="center"
        justifyContent="center"
        height="100vh"
        color="white"
        bg="#282c34"
      >
        {matchUps.length > 0 ? (
          <>
            <Flex
              direction="column"
              gap={4}
              alignItems="center"
              border="2px solid teal"
              p={4}
              borderRadius="md"
            >
              <Heading>Round: {round}</Heading>
              <Box
                border="1px solid teal"
                bg={"teal.50"}
                p={4}
                borderRadius="md"
              >
                <ButtonGroup gap="2">
                  <Button
                    colorScheme={"teal"}
                    size={"lg"}
                    onClick={() => set_outcome(0)}
                  >
                    {matchUps[current_index][0]}
                  </Button>
                  <Button
                    colorScheme={"teal"}
                    size={"lg"}
                    onClick={() => set_outcome(1)}
                  >
                    {matchUps[current_index][1]}
                  </Button>
                </ButtonGroup>
              </Box>
            </Flex>
            <Progress
              width={"100px"}
              mt="5"
              mb="5"
              colorScheme="green"
              size="md"
              max={matchUps.length}
              value={winners.length}
            />
          </>
        ) : (
          <Heading mb="4" size={"4xl"}>
            {winners[0]}
          </Heading>
        )}
        <Flex color="white">
          <Button onClick={() => init()}>Reset</Button>
          <Button ref={btnRef} onClick={onOpen} colorScheme="blue">
            Standings
          </Button>
        </Flex>
      </Flex>
      <Drawer
        isOpen={isOpen}
        placement="right"
        finalFocusRef={btnRef}
        onClose={onClose}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerHeader>Winners</DrawerHeader>
          <DrawerBody>
            {winners.map((w, i) => (
              <li key={i}>{w}</li>
            ))}
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
};

export default App;
